import React from 'react'
import { useSelector } from 'react-redux';
import io from "socket.io-client";
import './style.css'
import Common from '../../components/common/Common';

export default function Message() {
    const socket = io('https://eduket1-project1008.koyeb.app/');
    // const socket = io('localhost:3000');
    var name= useSelector((state)=> state.user?.myInfo.username);

    /*     */
// connect       
    socket.on('connect', function() {
    
        /*    */
        if(!name) {
            name = '';
        }
    
        /*      */
        socket.emit('newUser', name)
    })

    /*     */
    socket.on('update', function(data) {
        var chat = document.getElementById('chat')
        var message = document.createElement('div')
        var node = document.createTextNode(`${data.name}: ${data.message}`)
        var className = ''

      //      
        switch(data.type) {
        case 'message':
            className = 'other'
            break
    
        case 'connect':
            className = 'connect'
            break
    
        case 'disconnect':
            className = 'disconnect'
            break
    }
    
        message.classList.add(className)
        message.appendChild(node)
        chat.appendChild(message)
    })
    
    /*    */
    function send() {
        //   
        var message = document.getElementById('test').value
        
        //    
        document.getElementById('test').value = ''
    
        //     
        var chat = document.getElementById('chat')
        var msg = document.createElement('div')
        var node = document.createTextNode(message)
        msg.classList.add('me')
        msg.appendChild(node)
        chat.appendChild(msg)
    
        //  message   +  
        socket.emit('message', {type: 'message', message: message})
    }
    const pageTitle= '';
    const pageDesc= '   '
    const page= (
        <div id="main">
            <div className="main_div">
                <div id="chat"></div>
                <div className='text_div'>
                    <input type="text" id="test" placeholder=" ."/> 
                    <button className="btn" onClick={send}></button>
                </div>
            </div>
        </div>
    )
    return (
        <>
            <Common page={page} title={pageTitle} desc={pageDesc} />
        </>
    )
}